
const AccordionTitle = (props) => {
  const handleClick = (e) => {
    let target = e.target;
    if (target.className === 'Accordon-title') target.preventDefault()
    target.parentNode.nextElementSibling.classList.toggle('active')
    target.nextElementSibling.classList.toggle('active-span')
  }

  return (
    <div className="Accordion-title" onClick={handleClick}>
      <h2 className="Accordion-title__item">{props.title}</h2>
      <span></span>
    </div>
  )
}

const AccordionContent = (props) => {
  return (  
    <div className="Accordion-content">
      <p className="Accordion-content__description">
      {props.content}
      </p>
    </div>
  )
}

const AccordionItem = (props) => {
  
  return (
    <div className="AccordionItem">
      <AccordionTitle title={props.title}/>
      <AccordionContent content={props.content}/>
    </div>
  )
}

const Accordion = (props) => {
  

  return (
    <div className="Accordion">
      <AccordionItem title="Title #1" content="Lorem ipsum dolor...">
        <AccordionTitle />
        <AccordionContent />
      </AccordionItem>
      <AccordionItem title="Title #2" content="Lorem ipsum dolor...">
        <AccordionTitle />
        <AccordionContent  />
      </AccordionItem>
      <AccordionItem title="Title #3" content="Lorem ipsum dolor...">
        <AccordionTitle />
        <AccordionContent />
      </AccordionItem>
    </div>
  )
}

function App() {
  return (
    <div className="App">
      <Accordion />
    </div>
  );
}

export default App;
